body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

a {
  color: #0E1933;
  text-decoration: none; }
  a:hover {
    color: #CA9D8E; }

p a {
  color: white;
  background-color: #5B2333;
  border-radius: 3px;
  padding: 3px;
  font-weight: bold; }
  p a:hover {
    background: white;
    color: #5B2333; }

h1 {
  font-size: 100px;
  font-family: "Codystar", sans-serif;
  font-weight: 500;
  color: white; }

h2 {
  margin: 10px 0;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 25px;
  font-weight: 300;
  color: white; }

h3 {
  margin: 0;
  color: white;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 20px;
  font-weight: 300; }

p {
  margin: 0;
  color: white;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 25px; }

li {
  color: white;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 300;
  margin: 12.5px; }



